import React from 'react'
import Sky from '../img1.jpg'
import './Stylesheets/display.css'

export const Display = ({tracker}) => {
    return (
        <div className= "displaycontainer">
        <div className="pic"><img src={Sky} alt="pic"/></div> 
        <div className="imagetitle"> <h3>Image</h3>
            <p className="imagedate">{tracker.format("DD MMM YYYY")}</p>
             <p className="paragraph">Lorem ipsum dolor sit amet, consectetur
                adipiscing elit, sed do eiusmod tempor incididunt ut labore et
                dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco
                laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit
                in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat
                cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.</p> 
        </div> 
            <div className="post"> View Full Post</div>
        </div>
    )
}
